import { forwardRef, memo } from 'react';
import cn from 'classnames';

import { ButtonProps } from './Button.types';

const RCN = 'thuiButton';

export const Button = memo(
  forwardRef<HTMLButtonElement, ButtonProps>(
    (
      {
        title,
        kind = 'primary',
        size = 'md',
        onClick,
        disabled,
        className,
        iconLeft = null,
        iconRight = null,
        isLoading,
        loadingIcon,
        isSquare = false,
        type = 'button',
        ...rest
      }: ButtonProps,
      ref
    ) => {
      const btnCnString = cn(RCN, `${RCN}--${kind}`, `${RCN}--${size}`, className, {
        [`${RCN}--loading`]: isLoading,
        [`${RCN}--square`]: isSquare
      });

      const onClickHandler = isLoading ? undefined : onClick;

      const renderIconLeft = iconLeft && loadingIcon && isLoading ? loadingIcon : iconLeft;
      const renderIconRight = iconRight && loadingIcon && isLoading ? loadingIcon : iconRight;

      return (
        <button ref={ref} className={btnCnString} onClick={onClickHandler} disabled={disabled} type={type} {...rest}>
          {renderIconLeft && <div className={`${RCN}__leftIcon ${RCN}__icon`}>{renderIconLeft}</div>}
          {title && <div className={`${RCN}__title`}>{title}</div>}
          {renderIconRight && <div className={`${RCN}__rightIcon ${RCN}__icon`}>{renderIconRight}</div>}
        </button>
      );
    }
  )
);
